@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --var-back: rgb(255, 255, 255);
}
.container {
  height: 100vh;
}

.row {
  height: 100%;
}

body {
  background-color: rgb(255, 255, 255) !important;
  background-color: var(--var-back) !important;
  -webkit-transition: background-color 1s ease, color 0.2s linear;
  transition: background-color 1s ease, color 0.2s linear; 
}

.btn_for_background {
  background-color: rgb(255, 255, 255) !important;
  background-color: var(--var-back) !important;
  -webkit-transition: background-color 1s ease, color 0.2s linear;
  transition: background-color 1s ease, color 0.2s linear; 
}

.btn-primary:hover {
  border-color: rgb(255, 255, 255) !important;
  border-color: var(--var-back) !important;
}

.btn-primary {
  border-color: rgb(255, 255, 255) !important;
  border-color: var(--var-back) !important;
}

.btn-primary.focus, .btn-primary:focus {
  box-shadow: 0 0 0 0.2rem rgb(255, 255, 255) !important;
  box-shadow: 0 0 0 0.2rem var(--var-back) !important;
}

.conteiner-soc {
  width: 20%;
  display: flex;
  justify-content: space-between;
}

.btn_for_background_link {
  width: 2rem;
  height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 255, 255) !important;
  background-color: var(--var-back) !important;
}
